<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div class="home">
    <el-container class="home-container">
      <el-header height="40px">
        <top-mean></top-mean>
      </el-header>
      <el-container>
        <el-aside class="sidebarRight" width="36px">
          <img
            src="../assets/project-off.png"
            alt="project"
            title="project"
            @click="handProjectShow"
          />
          <img
            src="../assets/address-off.png"
            alt="Address Space"
            title="Address Space"
            @click="handAddressShow"
          />
          <img
            src="../assets/log-off.png"
            alt="Log"
            title="Log"
            @click="handLogShow"
          />
          <img
            src="../assets/attributes-off.png"
            alt="Attributes"
            title="Attributes"
            @click="handAttritubeShow"
          />
          <img
            src="../assets/references-off.png"
            alt="References"
            title="References"
            @click="handReferenceShow"
          />
          <img
            src="../assets/file-off.png"
            alt="File Toolbar"
            title="File Toolbar"
            @click="handFilesShow"
          />
          <img
            src="../assets/server-off.png"
            alt="Server Toolbar"
            title="Server Toolbar"
            @click="handServerShow"
          />
          <img
            src="../assets/document-off.png"
            alt="Document Toolbar"
            title="Document Toolbar"
            @click="handDocumentShow"
          />
        </el-aside>
        <el-container>
          <el-container ref="logRef">
            <el-aside
              class="subview"
              v-show="rightViewShowcomputed"
              width="240px"
            >
              <el-row v-show="projectShow">
                <el-col :span="24">
                  <div class="grid-content bg-purple">
                    <project-view></project-view>
                  </div>
                </el-col>
              </el-row>
              <el-row v-show="addressShow">
                <el-col :span="24">
                  <div class="grid-content bg-purple">
                    <address-view></address-view>
                  </div>
                </el-col>
              </el-row>
            </el-aside>
            <el-main>
              <data-view></data-view>
            </el-main>
            <el-aside
              class="subview"
              v-show="leftViewShowcomputed"
              width="240px"
            >
              <el-row v-show="attributesShow">
                <el-col :span="24">
                  <div class="grid-content bg-purple">
                    <attributes-view></attributes-view>
                  </div>
                </el-col>
              </el-row>
              <el-row v-show="referencesShow">
                <el-col :span="24"
                  ><div class="grid-content bg-purple">
                    <references-view></references-view></div
                ></el-col>
              </el-row>
            </el-aside>
          </el-container>
          <el-footer height="130px" v-show="logShow">
            <log-view></log-view>
          </el-footer>
        </el-container>
        <el-aside  class="sidebarLeft" width="28px">
          <right-sidebar></right-sidebar>
        </el-aside>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import topMean from '../components/topMean.vue'
import projectView from '../components/projectView.vue'
import addressView from '../components/addressView.vue'
import attributesView from '../components/attributesView.vue'
import referencesView from '../components/referencesView.vue'
import logView from '../components/logView.vue'
import dataView from '../components/dataView.vue'
import rightSidebar from '../components/sidebar/rightSidebar.vue'
// import leftSidebar from '../components/sidebar/leftSidebar.vue'
export default {
  // eslint-disable-next-line vue/no-unused-components
  components: {
    topMean,
    projectView,
    addressView,
    attributesView,
    referencesView,
    logView,
    dataView,
    rightSidebar
    // leftSidebar
  },
  data () {
    return {
      rightViewShow: true,
      projectShow: true,
      addressShow: true,
      leftViewShow: true,
      attributesShow: true,
      referencesShow: true,
      logShow: true
    }
  },
  methods: {
    handProjectShow () {
      this.projectShow = !this.projectShow
    },
    handAddressShow () {
      this.addressShow = !this.addressShow
    },
    handAttritubeShow () {
      this.attributesShow = !this.attributesShow
    },
    handReferenceShow () {
      this.referencesShow = !this.referencesShow
    },
    handLogShow () {
      this.logShow = !this.logShow
      const viewNodes = this.$refs.logRef.$children
      if (!this.logShow) {
        for (let i = 0; i < viewNodes.length; i++) {
          if (viewNodes[i].$children.length > 0) {
            for (let j = 0; j < viewNodes[i].$children.length; j++) {
              // console.log(viewNodes[i].$children[j].$children[0].$el.children[0].classList)
              viewNodes[i].$children[
                j
              ].$children[0].$el.children[0].classList.add('active1')
            }
          }
        }
      } else {
        for (let i = 0; i < viewNodes.length; i++) {
          if (viewNodes[i].$children.length > 0) {
            for (let j = 0; j < viewNodes[i].$children.length; j++) {
              viewNodes[i].$children[
                j
              ].$children[0].$el.children[0].classList.remove('active1')
            }
          }
        }
      }
    },
    handFilesShow () {
      console.log('hhh')
    },
    handServerShow () {
      console.log('hhh')
    },
    handDocumentShow () {
      console.log('hhh')
    }
  },
  mounted () {
    // console.log(this.$route);
  },
  computed: {
    rightViewShowcomputed: function () {
      // console.log(this.projectShow, '---', this.addressShow)
      if (!this.projectShow && !this.addressShow) {
        return !this.rightViewShow
      } else {
        return this.rightViewShow
      }
    },
    leftViewShowcomputed: function () {
      // console.log(this.projectShow, '---', this.addressShow)
      if (!this.attributesShow && !this.referencesShow) {
        return !this.leftViewShow
      } else {
        return this.leftViewShow
      }
    }
  }
}
</script>
<style lang="scss">
* {
  margin: 0px;
  padding: 0px;
}
.home {
  .home-container {
    width: 100%;
    height: 100%;
  }
  .el-header {
    background-color: #545c64;
    color: #333;
    text-align: center;
    line-height: 40px;
    padding: 0px;
  }

  .el-footer {
    background-color: #b3c0d1;
    color: #333;
    text-align: center;
    line-height: 130px;
    padding: 0px;
  }

  .el-aside {
    background-color: #d3dce6;
    color: #333;
    text-align: center;
  }

  .sidebarRight {
    background-color: #d3dce6;
    color: #333;
    text-align: center;
    height: 750px;
    img {
      margin-top: 10px;
      width: 24px;
      height: 24px;
    }
  }

  .subview {
    // height: 620px;
    background-color: #babcbd;
    color: #333;
    text-align: center;
    .bg-purple {
      height: 310px;
      width: 240px;
      background: #cdc6c6;
    }
    .active1 {
      height: 374px;
      width: 240px;
      background: #cdc6c6;
    }
    .active2 {
      height: 100%;
      width: 240px;
      background: #cdc6c6;
    }
  }

  .el-main {
    background-color: #e9eef3;
    color: #333;
    text-align: center;
    padding: 5px 8px;
  }
}
</style>
