<template>
  <div class="referencesView">
    <div class="headerTitle">references</div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <i
          class="el-icon-refresh operationNode"
          style="color: #fe5f00"
          title="Refresh"
        ></i>
        <i class="el-icon-check operationNode" title="On/Off"></i>
        <i
          class="el-icon-menu operationNode"
          title="Show Hierarchical References"
        ></i>
        <i
          class="el-icon-s-grid operationNode"
          title="Show Non Hierarchical References"
        ></i>
        <el-select v-model="referenceName" placeholder="Forward">
          <el-option
            v-for="item in addressUrlList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
        <i
          class="el-icon-circle-plus addNode"
          title="Set Custom Nodeld"
          @click="handSetNode"
        ></i>
      </div>
      <div class="text item">
        <el-table
          :data="tableData"
          row-key="id"
          height="100%"
          stripe
          border
          :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
        >
          <el-table-column
            show-overflow-tooltip
            prop="references"
            label="Reference"
            width="120px"
          >
          </el-table-column>
          <el-table-column
            prop="targetName"
            label="Target DisplayName"
            width="240px"
          >
          </el-table-column>
        </el-table>
      </div>
    </el-card>
    <setcustomld-view
      v-if="setNodeVisible"
      ref="setNodeDialog"
    ></setcustomld-view>
  </div>
</template>
<script>
import setcustomldView from './popover/setcustomldView.vue'
export default {
  components: {
    setcustomldView
  },
  data () {
    return {
      setNodeVisible: false,
      referenceName: '',
      addressUrlList: [
        {
          value: '0',
          label: 'Forward'
        },
        {
          value: '1',
          label: 'Inverse'
        },
        {
          value: '2',
          label: 'Both'
        }
      ],
      tableData: [
        {
          id: 1,
          references: 'HasTypeDefinition',
          targetName: 'BaseDataVariableType',
          children: [
            {
              id: 11,
              references: 'Namespacelndex',
              targetName: '3, "counter"'
            },
            {
              id: 12,
              references: 'ldentifierType',
              targetName: '3, "counter"'
            },
            {
              id: 13,
              references: 'ldentifier',
              targetName: '3, "counter"'
            }
          ]
        },
        {
          id: 2,
          references: 'HasSimulation',
          targetName: 'ValueSimulation'
        }
      ]
    }
  },
  methods: {
    handSetNode () {
      this.setNodeVisible = true
      this.$nextTick(() => {
        this.$refs.setNodeDialog.initSetNode()
      })
    }
  }
}
</script>
<style lang="ssss" scoped></style>
<style lang="scss">
.referencesView {
  height: 100%;
  .headerTitle {
    height: 5%;
    font-size: 12px;
    text-align: left;
    background-color: #e5e5e5;
  }
  .box-card {
    width: 99%;
    height: 93%;
    border-radius: 0%;
    .el-card__header {
      padding: 5px;
      height: 10%;
      .clearfix {
        font-size: 16px;
        height: 100%;
        line-height: 100%;
        .operationNode {
          float: left;
          width: 10%;
        }
        .addNode {
          float: right;
        }
        .el-select {
          height: 20px;
          width: 40%;
          font-size: 10px;
          .el-input {
            .el-input__icon {
              line-height: 100%;
            }
            .el-input__inner {
              height: 20px;
              line-height: 20px;
              padding: 0px;
              font-size: 12px;
              padding-left: 5px;
            }
            .el-input__suffix {
              right: 0px;
            }
          }
        }
      }
    }
    .el-card__body {
      width: 100%;
      height: 90%;
      padding: 0px;
      background-color: aquamarine;
      .item {
        height: 100%;
      }
    }
  }
  .el-table {
    [class*="el-table__row--level"] .el-table__expand-icon {
      margin-right: 0px;
    }
    .el-table__cell {
      padding: 0px;
      .cell {
        padding-right: 0px;
        .el-table__indent {
          display: none;
        }
      }
    }
  }
}
</style>
