<template>
  <div class="topMean">
    <el-menu
      :default-active="activeIndex"
      class="el-menu-demo"
      mode="horizontal"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
    >
      <el-submenu index="1">
        <template slot="title">File</template>
        <el-menu-item index="1-1">New</el-menu-item>
        <el-menu-item index="1-2">Open</el-menu-item>
        <el-menu-item index="1-3">Save</el-menu-item>
        <el-menu-item index="1-4">Save As</el-menu-item>
        <el-menu-item index="1-5">Recent Projects</el-menu-item>
        <el-menu-item index="1-6">Quit</el-menu-item>
      </el-submenu>
      <el-submenu index="2">
        <template slot="title">View</template>
        <el-menu-item index="2-1" @click="handProjectShow"
          >Project</el-menu-item
        >
        <el-menu-item index="2-2" @click="handAddressShow"
          >Address Space</el-menu-item
        >
        <el-menu-item index="2-3" @click="handLogShow">Log</el-menu-item>
        <el-menu-item index="2-4" @click="handAttritubeShow"
          >Attributes</el-menu-item
        >
        <el-menu-item index="2-5" @click="handReferenceShow"
          >References</el-menu-item
        >
        <el-menu-item index="2-6" @click="handProjectShow"
          >File Toolbar</el-menu-item
        >
        <el-menu-item index="2-7" @click="handProjectShow"
          >Server Toolbar</el-menu-item
        >
        <el-menu-item index="2-8" @click="handProjectShow"
          >Document Toolbar</el-menu-item
        >
      </el-submenu>
      <el-submenu index="3">
        <template slot="title">Server</template>
        <el-menu-item index="3-1" @click="handAddserver">Add</el-menu-item>
        <el-menu-item index="3-2">Remove</el-menu-item>
        <el-menu-item index="3-3">Connect</el-menu-item>
        <el-menu-item index="3-4">Disconnect</el-menu-item>
        <el-menu-item index="3-5">PRoperties</el-menu-item>
        <el-menu-item index="3-6">Change user</el-menu-item>
      </el-submenu>
      <el-submenu index="4">
        <template slot="title">Document</template>
        <el-menu-item index="4-1" @click="handAdddocument">Add</el-menu-item>
        <el-menu-item index="4-2">Remove</el-menu-item>
        <el-menu-item index="4-3">Hide / unhide all dock widgets</el-menu-item>
      </el-submenu>
      <el-submenu index="5">
        <template slot="title">Settings</template>
        <el-menu-item index="5-1" @click="handPlugin">Plugins</el-menu-item>
        <el-menu-item index="5-2" @click="handConfigure"
          >Configure UaExpert</el-menu-item
        >
        <el-menu-item index="5-3">Manage Certificates</el-menu-item>
      </el-submenu>
      <el-submenu index="6">
        <template slot="title">Help</template>
        <el-menu-item index="6-1">UaExpert Manual</el-menu-item>
        <el-menu-item index="6-2">What's This?</el-menu-item>
        <el-menu-item index="6-3">Report a bug</el-menu-item>
        <el-menu-item index="6-4">License Status</el-menu-item>
        <el-menu-item index="6-5">Check for Updates</el-menu-item>
        <el-menu-item index="6-6">About UaExpert</el-menu-item>
      </el-submenu>
    </el-menu>
    <add-server-view v-if="addSerVisible" ref="addSerDialog"></add-server-view>
    <add-document-view
      v-if="addDocVisible"
      ref="addDocDialog"
    ></add-document-view>
    <plugin-view v-if="pluginVisible" ref="pluginDialog"></plugin-view>
    <configure-view
      v-if="configureVisible"
      ref="configureDialog"
    ></configure-view>
  </div>
</template>
<script>
import addServerView from './popover/addServerView.vue'
import addDocumentView from './popover/addDocumentView.vue'
import pluginView from './popover/pluginView.vue'
import configureView from './popover/configureView.vue'
export default {
  components: {
    addServerView,
    addDocumentView,
    pluginView,
    configureView
  },
  data () {
    return {
      addSerVisible: false,
      addDocVisible: false,
      pluginVisible: false,
      configureVisible: false,
      activeIndex: '1'
    }
  },
  methods: {
    handProjectShow () {
      // console.log(this.$parent.$parent.$parent)
      this.$parent.$parent.$parent.handProjectShow()
    },
    handAddressShow () {
      this.$parent.$parent.$parent.handAddressShow()
    },
    handLogShow () {
      this.$parent.$parent.$parent.handLogShow()
    },
    handAttritubeShow () {
      this.$parent.$parent.$parent.handAttritubeShow()
    },
    handReferenceShow () {
      this.$parent.$parent.$parent.handReferenceShow()
    },
    handAddserver () {
      this.addSerVisible = true
      this.$nextTick(() => {
        this.$refs.addSerDialog.initAddserver()
      })
    },
    handAdddocument () {
      this.addDocVisible = true
      this.$nextTick(() => {
        this.$refs.addDocDialog.initAddDoc()
      })
    },
    handPlugin () {
      this.pluginVisible = true
      this.$nextTick(() => {
        this.$refs.pluginDialog.initPlugin()
      })
    },
    handConfigure () {
      this.configureVisible = true
      this.$nextTick(() => {
        this.$refs.configureDialog.initConfigure()
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.el-menu--horizontal ::v-deep .el-submenu .el-submenu__title {
  height: 40px;
  line-height: 40px;
  color: #909399;
}
</style>
<style lang="scss">
.topMean {
  width: 100%;
  .msgboxServer {
    width: 100%;
  }
  @media (min-width: 768px) {
    .msgboxServer {
      width: 600px !important;
    }
  }
}
</style>
