<template>
  <div class="advancedSubView">
    <div class="serInformation setBox">
      <div class="borderTxt">
        <span class="lineLeft"></span>
        <span>Server Information</span>
        <span class="lineRight" style="width: 358px"></span>
      </div>
      <div style="padding: 0 15px">
        <div class="pointBox">
          <span class="contentName">Endpoint Url</span>
          <input class="contentInput" type="text" />
        </div>
        <div class="reverseBox">
          <span class="contentName">Reverse Connect</span>
          <input class="radioContentInput" type="checkbox" />
        </div>
      </div>
    </div>
    <div class="securitySet setBox">
      <div class="borderTxt">
        <span class="lineLeft"></span>
        <span>Security Settings</span>
        <span class="lineRight" style="width: 368px"></span>
      </div>
      <div style="padding: 0 15px">
        <div class="policyBox">
          <span class="contentName">Security Policy</span>
          <el-select v-model="pointFilterName" placeholder="None">
            <el-option
              v-for="item in pointFilterList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
        <div class="modeBox">
          <span class="contentName longContent">Message Security Mode</span>
          <el-select v-model="pointFilterName" placeholder="None">
            <el-option
              v-for="item in pointFilterList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
      </div>
    </div>
    <div class="authenticationSet setBox">
      <div class="borderTxt">
        <span class="lineLeft"></span>
        <span>Authentication Settings</span>
        <span class="lineRight" style="width: 330px"></span>
      </div>
      <div style="padding: 0 15px">
        <div class="anonymousBox">
          <div class="radioDefined">
            <input
              class="radioBox"
              type="radio"
              v-model="verification"
              label="1"
            />
            <div class="radioContent">Anonymous</div>
          </div>
        </div>
        <div class="accountBox">
          <div class="radioDefined">
            <input
              class="radioBox"
              type="radio"
              v-model="verification"
              label="2"
            />
            <div class="radioContent">
              <div class="radioContentTop">
                <span class="radioContentName">Username</span>
                <input class="radioContentInput" type="text" />
                <span class="storeCheck">
                  <input type="checkbox" id="storeChecked" />
                  <label for="storeChecked">store</label>
                </span>
              </div>
              <div class="radioContentBottom">
                <span class="radioContentName">Password</span>
                <input class="radioContentInput" type="text" />
              </div>
            </div>
          </div>
        </div>
        <div class="certificateBox">
          <div class="radioDefined">
            <input
              class="radioBox"
              type="radio"
              v-model="verification"
              label="3"
            />
            <div class="radioContent">
              <div class="radioContentTop">
                <span class="radioContentName">Certificate</span>
                <input class="radioContentInput" type="text" />
                <button class="moreBtn">
                  <i class="el-icon-more"></i>
                </button>
              </div>
              <div class="radioContentBottom">
                <span class="radioContentName">Private Key</span>
                <input class="radioContentInput" type="text" />
                <button class="moreBtn">
                  <i class="el-icon-more"></i>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="sessionSet setBox">
      <div class="borderTxt">
        <span class="lineLeft"></span>
        <span>Session Settings</span>
        <span class="lineRight" style="width: 369px"></span>
      </div>
      <div style="padding: 0 15px">
        <div class="sessionNameBox">
          <span class="contentName">Session Name</span>
          <input class="contentInput" type="text" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      verification: '1',
      pointFilterName: '',
      pointFilterList: [
        {
          value: '0',
          label: 'No Filter'
        },
        {
          value: '1',
          label: 'opc.tcp'
        },
        {
          value: '2',
          label: 'http'
        },
        {
          value: '3',
          label: 'https'
        }
      ]
    }
  },
  methods: {}
}
</script>
<style lang="ssss" scoped></style>
<style lang="scss">
