      width: 26px;
      height: 3px;
      margin-right: 4px;
      display: inline-block;
      border-top: 1px solid #b8b8b8;
    }
    .lineRight {
      height: 3px;
      margin-left: 4px;
      display: inline-block;
      border-top: 1px solid #b8b8b8;
    }
  }
  .contentName {
    width: 123px;
