    height: 60px;
    line-height: 20px;
  }
  .serInformation {
    height: 70px;
    .pointBox {
      height: 35px;
      line-height: 50px;
      // margin-top: 5px;
      text-align: start;
    }
    .reverseBox {
      height: 35px;
      line-height: 35px;
      text-align: start;
    }
  }
  .securitySet {
    height: 100px;
    .policyBox {
      height: 40px;
      line-height: 55px;
      text-align: start;
      .el-select{
        .el-input__inner{
          width: 300px;
          height: 30px;
        }
      }
    }
    .modeBox {
      height: 60px;
      line-height: 60px;
      margin-top: 10px;
      text-align: start;
      .el-select{
        .el-input__inner{
          width: 300px;
          height: 30px;
        }
      }
    }
  }
  .authenticationSet {
    height: 200px;
    .anonymousBox {
      height: 50px;
      line-height: 50px;
      text-align: start;
      border-bottom: 1px solid #b8b8b8;
    }
    .accountBox {
      height: 72px;
      text-align: start;
      border-bottom: 1px solid #b8b8b8;
    }
    .certificateBox {
      height: 72px;
      text-align: start;
    }
    .radioDefined {
      height: 100%;
    }
    .radioBox {
      height: 100%;
