      float: left;
      .radioContentTop {
        height: 50%;
        .storeCheck {
          margin-left: 8px;
          display: inline-block;
          #storeChecked {
            margin: auto 3px;
            // transform:scale(0.8)
          }
        }
      }
      .radioContentBottom {
        height: 50%;
      }
      .radioContentName {
        width: 100px;
        display: inline-block;
      }
      .radioContentInput {
        width: 260px;
        border: 1px solid #b8b8b8;
      }
      .moreBtn {
        width: 50px;
