        margin-left: 10px;
      }
    }
    .el-radio {
      width: 120px;
      .el-radio__input {
        float: left;
      }
      .el-radio__label {
        display: block;
        float: left;
        .location {
          margin: 10px 0;
        }
      }
    }
  }
  .sessionSet {
    height: 60px;
    .sessionNameBox {
      height: 60px;
      line-height: 60px;
      text-align: start;
    }
  }
