<template>
  <div class="discoverySubView">
    <div class="filterBox">
      Endpoint Filter:
      <el-select v-model="pointFilterName" placeholder="No Filter">
        <el-option
          v-for="item in pointFilterList"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
    </div>
    <div class="serverBox">
      <el-menu default-active="1" class="el-menu-vertical-demo">
        <el-menu-item index="1">
          <i class="el-icon-search"></i>
          Local
        </el-menu-item>
        <el-submenu index="2">
          <template slot="title">
            <i class="el-icon-s-platform"></i>
            <span>Local Network</span>
          </template>
          <el-submenu index="2-1">
            <template slot="title">
              <i class="el-icon-s-platform"></i>
              Aliases
            </template>
            <el-submenu index="2-1-1">
              <template slot="title">
                <i class="el-icon-s-platform"></i>
                FindAlias
              </template>
              <el-menu-item
                index="2-1-1-1"
                v-for="item in serverList"
                :key="item.serverName"
                >{{ item.serverName }}</el-menu-item
              >
            </el-submenu>
            <el-submenu index="2-1-2">
              <template slot="title">
                <i class="el-icon-s-platform"></i>
                TagVariables
              </template>
            </el-submenu>
            <el-submenu index="2-1-3">
              <template slot="title">
                <i class="el-icon-s-platform"></i>
                Topics
              </template>
            </el-submenu>
          </el-submenu>
          <el-submenu index="2-2">
            <template slot="title">
              <i class="el-icon-s-platform"></i>
              MyObjects
            </template>
          </el-submenu>
          <el-submenu index="2-3">
            <template slot="title">
              <i class="el-icon-s-platform"></i>
              MyObjects
            </template>
          </el-submenu>
          <el-submenu index="2-4">
            <template slot="title">
              <i class="el-icon-s-platform"></i>
              MyObjects
            </template>
          </el-submenu>
        </el-submenu>
        <el-submenu index="3">
          <template slot="title">
            <i class="el-icon-s-platform"></i>
            <span>Reverse Discovery</span>
          </template>
          <el-submenu index="3-1">
            <template slot="title">
              <i class="el-icon-plus"></i>EventTypes</template
            >
          </el-submenu>
          <el-submenu index="3-2">
            <template slot="title">
              <i class="el-icon-s-platform"></i>DataTypes
            </template>
            <el-submenu index="3-2-1">
              <template slot="title">
                <i class="el-icon-s-platform"></i>
                FindAlias
              </template>
              <el-menu-item
                index="3-2-1-1"
                v-for="item in serverList"
                :key="item.serverName"
                >{{ item.serverName }}</el-menu-item
              >
            </el-submenu>
          </el-submenu>
        </el-submenu>
        <el-submenu index="4">
          <template slot="title">
            <i class="el-icon-s-platform"></i>
            <span>custom Discovery</span>
          </template>
          <el-submenu index="4-1">
            <template slot="title">
              <i class="el-icon-plus"></i>EventTypes</template
            >
          </el-submenu>
          <el-submenu index="4-2">
            <template slot="title">
              <i class="el-icon-search"></i>DataTypes
            </template>
            <el-submenu index="4-2-1">
              <template slot="title">
                <i class="el-icon-s-platform"></i>
                FindAlias
              </template>
              <el-menu-item
                index="4-2-1-1"
                v-for="item in serverList"
                :key="item.serverName"
                >{{ item.serverName }}</el-menu-item
              >
            </el-submenu>
          </el-submenu>
        </el-submenu>
        <el-submenu index="5">
          <template slot="title">
            <i class="el-icon-time"></i>
            <span>Recently Used</span>
          </template>
        </el-submenu>
      </el-menu>
    </div>
    <div class="verificationBox">
      <div class="borderTxt">
        <span class="lineLeft"></span>
        <span>Authentication Settings</span>
        <span class="lineRight"></span>
      </div>
      <div style="padding: 0 15px">
        <div class="anonymousBox">
          <div class="radioDefined">
            <input
              class="radioBox"
              type="radio"
              v-model="verification"
              label="1"
            />
            <div class="radioContent">Anonymous</div>
          </div>
        </div>
        <div class="accountBox">
          <div class="radioDefined">
            <input
              class="radioBox"
              type="radio"
              v-model="verification"
              label="2"
            />
            <div class="radioContent">
              <div class="radioContentTop">
                <span class="radioContentName">Username</span>
                <input class="radioContentInput" type="text" />
                <span class="storeCheck">
                  <input type="checkbox" id="storeChecked" />
                  <label for="storeChecked">store</label>
                </span>
              </div>
              <div class="radioContentBottom">
                <span class="radioContentName">Password</span>
                <input class="radioContentInput" type="text" />
              </div>
            </div>
          </div>
        </div>
        <div class="certificateBox">
          <div class="radioDefined">
            <input
              class="radioBox"
              type="radio"
              v-model="verification"
              label="3"
            />
            <div class="radioContent">
              <div class="radioContentTop">
                <span class="radioContentName">Certificate</span>
                <input class="radioContentInput" type="text" />
                <button class="moreBtn">
                  <i class="el-icon-more"></i>
                </button>
              </div>
              <div class="radioContentBottom">
                <span class="radioContentName">Private Key</span>
                <input class="radioContentInput" type="text" />
                <button class="moreBtn">
                  <i class="el-icon-more"></i>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      verification: '1',
      pointFilterName: '',
      pointFilterList: [
        {
          value: '0',
          label: 'No Filter'
        },
        {
          value: '1',
          label: 'opc.tcp'
        },
        {
          value: '2',
          label: 'http'
        },
        {
          value: '3',
          label: 'https'
        }
      ],
      serverList: [
        { serverName: 'SimulationServer@DESKTOP-REOKHRI' },
        { serverName: 'SimulationServer@REOKHRI-DESKTOP' }
      ]
    }
  },
  methods: {}
}
</script>
<style lang="ssss" scoped></style>
<style lang="scss">
.discoverySubView {
  padding: 5px 10px;
  .filterBox {
    height: 30px;
    line-height: 30px;
    font-size: 12px;
    .el-select {
      height: 100%;
      .el-input--suffix {
        height: 20px;
        line-height: 20px;
        margin: 5px 10px;
        .el-input__inner {
          width: 360px;
          height: 100%;
        }
        .el-input__icon {
          line-height: 20px;
          margin-right: 10px;
        }
      }
    }
  }
  .serverBox {
    height: 300px;
    border: 2px solid #b8b8b8;
    background-color: white;
    overflow-y: scroll;
    overflow-x: scroll;
    .el-menu {
      //   height: 100%;
      .el-menu-item {
        text-align: left;
        height: 40px;
        line-height: 40px;
      }
      .el-submenu {
        .el-submenu__title {
          text-align: left;
          height: 40px;
          line-height: 40px;
        }
      }
    }
    .el-menu-item-group__title {
      padding-left: 0px;
    }
  }
  .verificationBox {
    height: 200px;
    margin: 15px 5px 5px;
    border: 1px solid #b8b8b8;
    border-top: none;
    position: relative;
    .borderTxt {
      position: absolute;
      top: -22px;
      .lineLeft {
        width: 26px;
        height: 3px;
        margin-right: 4px;
        display: inline-block;
        border-top: 1px solid #b8b8b8;
      }
      .lineRight {
        width: 310px;
        height: 3px;
        margin-left: 4px;
        display: inline-block;
        border-top: 1px solid #b8b8b8;
      }
    }
    .anonymousBox {
      height: 50px;
      line-height: 50px;
      text-align: start;
      border-bottom: 1px solid #b8b8b8;
    }
    .accountBox {
      height: 72px;
      text-align: start;
      border-bottom: 1px solid #b8b8b8;
    }
    .certificateBox {
      height: 72px;
      text-align: start;
    }
    .radioDefined {
      height: 100%;
    }
    .radioBox {
      height: 100%;
      float: left;
    }
    .radioContent {
      height: 100%;
      margin-left: 10px;
      float: left;
      .radioContentTop {
        height: 50%;
        .storeCheck {
          margin-left: 8px;
          display: inline-block;
          #storeChecked {
            margin: auto 3px;
            // transform:scale(0.8)
          }
        }
      }
      .radioContentBottom {
        height: 50%;
      }
      .radioContentName {
        width: 100px;
        display: inline-block;
      }
      .radioContentInput {
        width: 260px;
        border: 1px solid #b8b8b8;
      }
      .moreBtn {
        width: 50px;
        height: 19px;
        margin-left: 10px;
      }
    }
    .el-radio {
      width: 120px;
      .el-radio__input {
        float: left;
      }
      .el-radio__label {
        display: block;
        float: left;
        .location {
          margin: 10px 0;
        }
      }
    }
  }
  ::-webkit-scrollbar {
    -webkit-appearance: none;
    width: 5px;
    height: 5px;
  }
}
</style>
